import { ApplicationCommand, Client } from "discord.js";

export class ApplicationCommandHandler {
  private _commands: Map<string, ApplicationCommand> = new Map();

  constructor(client: Client, files: string[]) {
    (async () => {
      for (const file of files) {
        let command = await import(file);

        if (command.default) command = command.default;
        else if (command.command) command = command.command;

        if (!command.isSlash()) continue;
      }
    })();
  }
}
